#slider {
    transform: translateY(10%);
    overflow: hidden;
    border: black;
    margin-left: auto;
    box-shadow: 0 0 10px rgba(0, 0, 0, 2);
}

#slider figure{
    
    position: relative;
    width: 500%;
    margin: 0;
    left: 0;
    animation: 20s slider infinite;
}

#slider figure img {
    margin-top: -0.5%;
    width: 20%;
    height: 100%;
    float: right;
    margin-bottom: -3%;
}

@keyframes slider{
    0% {
        left: 0;
    }
    20% {
        left: 0;
    }
    25% {
        left: -100%;
    }
    45% {
        left: -100%;
    }
    50% {
        left: -200%;
    }
    70% {
        left: -200%;
    }
    75% {
        left: -300%;
    }
    95% {
        left: -300%;
    }
    100% {
        left: -400%;
    }
}

@media screen and (max-width: 768px){

    #slider{
        transform: translateY(-10%);
        width: 60%;
        right: 0;
    }
    #slider figure{
        
        width: 500%;
        
    }

    #slider figure img {
        margin-top: -0.5%;
        width: 20%;
        height: 100%;
        float: left;
        margin-bottom: -3%;
    }
}