section
{
    position: relative;
    width: 100%;
    height: 1000px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(-85%);
    z-index: 0;
    overflow: hidden;
    opacity: 0.6;
    
    
}

section img
{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media screen and (max-width: 768px) {
    section{
        opacity: 0.3;
    }
}