.cont ul{
    position: relative;
    display: flex;
    transform-style: preserve-3d;
    transform: rotate(-25deg) skew(25deg);
}
.cont ul li{
    position: relative;
    list-style: none;
    width: 60px;
    height: 60px;
    margin: 0 25px;
    
}
@media screen and (max-width:768px) {
    .cont ul li{
        width: 45px;
        height: 45px;
        margin: 0 15px;
    }
}

.cont ul li:before{
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 100%;
    height: 10px;
    background: #2a2a2a;
    transform-origin: top;
    transform: skewX(-41deg);
}
.cont ul li:after{
    content: '';
    position: absolute;
    top: 0;
    left: -9px;
    width: 9px;
    height: 100%;
    background: #2a2a2a;
    transform-origin: right;
    transform: skewY(-49deg);
}

.cont ul li span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #333;
    color: rgba(255, 255, 255, 0.2);
    font-size: 30px !important;
    transition: 0.2s;
}
.cont ul li:hover span{
    z-index: 1000;
    transition: 0.5s;
    color: #fff;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.05);
}
.cont ul li:hover span:nth-child(5){
    transform: translate(40px, -40px);
    opacity: 1;
}
.cont ul li:hover span:nth-child(4){
    transform: translate(30px, -30px);
    opacity: 0.8;
}
.cont ul li:hover span:nth-child(3){
    transform: translate(20px, -20px);
    opacity: 0.6;
}
.cont ul li:hover span:nth-child(2){
    transform: translate(10px, -10px);
    opacity: 0.4;
}
.cont ul li:hover span:nth-child(1){
    transform: translate(0px, 0px);
    opacity: 0.2;
}

.cont ul li:nth-child(1):hover span{
    background: #3b5999;
}
.cont ul li:nth-child(2):hover span{
    background: #dd5745;
}
.cont ul li:nth-child(3):hover span{
    background: #DD2A7B;
}
.cont ul li:nth-child(4):hover span{
    background: #4c80f1;
}
.cont ul li:nth-child(5):hover span{
    background: #f5f5f5;
}

.cont img{
    height: 70%;
    margin: auto;
}

.cont a{
    cursor: pointer;
}



